import React from 'react';
import moment from "moment";
import { useDispatch } from 'react-redux';
import { activeNote } from "../../actions/notes";
export const JornalEntry = ({ id, title, body, date, url, }) => {
    const noteDate = moment(date);
    const dispatch = useDispatch();
    const handleEntryClick = () => {

        dispatch(activeNote(id, { title, body, date, url}))
    }
    return (
        <div>
            <div className="journal__entry pointer animate__animated animate__bounceInLeft animate__faster" onClick={handleEntryClick}>

                {url &&
                    <div
                        className="journal__entry-picture"
                        style={{
                            backgroundSize: 'cover',
                            backgroundImage: `url(${url})`,
                        }}
                    />
                }
                <div className="journal__entry-body">
                    <p className="journal__entry-title">
                        {title}
                    </p>
                    <p className="journal__entry-content">
                        {body}
                    </p>
                </div>
                <div className="journal__entry-date-box">
                    <span>{noteDate.format('dddd')}</span>
                    <h4>{noteDate.format('do')}</h4>
                </div>

            </div>
        </div>
    )
}
